import { Component, OnInit, Output, EventEmitter,Input } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { InterfaceHttpService } from '../../../http/interface.http.servive';
import { BusinessScenceMaintainAddBean } from '../bean/BusinessScenceMaintain.bean';
/**
 * 页面维护新增
 */
@Component({
    selector: 'business-maintain-update',
    templateUrl: './business-maintain-update.component.html',
    styleUrls: ['./business-maintain-update.component.scss'],
})

export class BusinessMaintainUpdateComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    businessScenceMaintainAddBean:BusinessScenceMaintainAddBean = new BusinessScenceMaintainAddBean();
    msgs:any;
    constructor(
        public interfaceHttpService:InterfaceHttpService
    ) {
        
    }

    ngOnInit() {
        this.businessScenceMaintainAddBean = this.inValue
    }

    save() {
        if(!this.businessScenceMaintainAddBean.businessSenceId){
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "请输入业务场景ID" });
            return
        }
        if(!this.businessScenceMaintainAddBean.businessSenceName){
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "请输入业务场景名称" });
            return
        }
        this.interfaceHttpService.updateBusinessSenceDefinition(this.businessScenceMaintainAddBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功' })
                this.outValue.emit(false)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

     //重置
     reset() {
        this.businessScenceMaintainAddBean.businessSenceName = '';
    }

    //取消
    cancel(){
        this.outValue.emit(false)
    }


    
}