import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from '../../../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../../../@uisftech/common/constant/config';
import { PageScenceSetBean } from '../../bean/BusinessScenceMaintain.bean';
import { InterfaceHttpService } from '../../../../http/interface.http.servive';
/**
 * 页面集设置
 */
@Component({
    selector: 'page-scence-rule',
    templateUrl: './page-scence-rule.component.html',
    styleUrls: ['./page-scence-rule.component.scss'],
})

export class pageScenceRuleComponent implements OnInit {
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    @Input() inValue: any;
    tableList:any=[];
    pageScenceSetBean:PageScenceSetBean = new PageScenceSetBean();
    msgs:any;
    //分页
    total: number;
    first: number = 0;
    checkValue:any = []
    arr:any = []
    constructor(
        public interfaceHttpService:InterfaceHttpService
    ) {

    }

    ngOnInit() {
        this.checkValue = []
        this.inValue.forEach(element => {
            this.checkValue.push(element.pageCollectId)
        });
        this.arr = this.inValue
        this.pageScenceSetBean.pageNum = 1;
        this.pageScenceSetBean.pageSize = 10;
        this.query()
    }

     //查询
     query() {
        this.interfaceHttpService.queryPageCollectDefinitionList(this.pageScenceSetBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList = data.pageCollectList;
                this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    select(event,item) {
        if(event == true){
            this.arr.push(item)
        }
        if(event ==false) {
            this.arr.forEach((element,index) => {
                if(element.pageCollectId == item.pageCollectId){
                    this.arr.splice(index,1)
                }
            });
        }
    }

    //确定
    sure () {
        this.outValue.emit(this.arr)
    }
    
    //分页
    paginate(event) {
        this.pageScenceSetBean.pageSize = event.rows;//每页显示的条数
        let currentPage = event.page + 1; //当前页
        this.pageScenceSetBean.pageNum = currentPage;
        this.first = event.page * event.rows;
        this.query();//调用查询的方法
    }

    reset() {
        this.pageScenceSetBean.pageCollectName = '';
    }

}