import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { tableMessage } from '../../../../../../../@uisftech/common/constant/config';
import { InterfaceHttpService } from '../../../http/interface.http.servive';

@Component({
    selector: 'business-data-set',
    templateUrl: './business-data-set.component.html',
    styleUrls: ['./business-data-set.component.scss'],
    providers: [InterfaceHttpService,ConfirmationService]
})

export class BusinessDataSetComponent implements OnInit {
    tableList: any = [];
    detailDisplay: boolean = false;
    businessSenceId:string;
    msgs:any;
    scenceRule:any;
    constructor(
        public commfunc: Commfunc,
        public confirmationService:ConfirmationService,
        public activatedRouter: ActivatedRoute,
        public interfaceHttpService:InterfaceHttpService,
        public router:Router,
    ) {


    }

    ngOnInit() {
        this.activatedRouter.queryParams.subscribe(params => {
            this.businessSenceId = params.businessSenceId;
          })
          this.query()
    }

    //查询
    query() {
        this.interfaceHttpService.pageCollectListByBusinessId({businessSenceId:this.businessSenceId}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableList = data;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }


    //页面集配置
    addieldTemplate() {
        if(!this.tableList.pageCollectList){
            this.tableList.pageCollectList = []
        }
        this.scenceRule = this.tableList.pageCollectList;
        this.detailDisplay = true;
    }

    //配置回掉
    ruleCall(event) {
        this.detailDisplay = false;
        if(!this.tableList.pageCollectList){
            this.tableList.pageCollectList=[]
        }
        this.tableList.pageCollectList = _.clone(event)
    }

    //删除
    deleteTemplate(i) {
        this.tableList.pageCollectList.splice(i, 1);
        this.tableList.pageCollectList = _.clone(this.tableList.pageCollectList);
    }

    //保存
    save() {
        if((!this.tableList.pageCollectList)||(this.tableList.pageCollectList.length=='0')){
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请配置页面集' });
            return
        }
        let arr = [];
        this.tableList.pageCollectList.forEach(element => {
            let obj = {};
            obj['pageCollectId'] = element.pageCollectId;
            arr.push(obj)
        });
        this.interfaceHttpService.dualBusinessSenceCollectAssoc({businessSenceId:this.businessSenceId,pageCollectList:arr}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.query();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '保存成功' })
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    reset(){
        this.tableList.pageCollectList = []
    }

    cancel() {
        this.router.navigate(['/pages/tzb/page-layout/business-scence/business-scence-maintain'])
    }
}