import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { tableMessage } from '../../../../../../@uisftech/common/constant/config';
import { BusinessScenceMaintain } from './bean/BusinessScenceMaintain.bean';
import { InterfaceHttpService } from '../../http/interface.http.servive';


/**
 * 页面维护界面
 */
@Component({
    selector: 'business-scence-maintain',
    templateUrl: './business-scence-maintain.component.html',
    styleUrls: ['./business-scence-maintain.component.scss'],
    providers:[ConfirmationService]
})
/**
 * 业务场景
 */
export class BusinessScenceMaintainComponent implements OnInit {
    tableData: any = [];
    updateDisplay: boolean = false;
    detailDisplay: boolean = false;
    elementDisplay: boolean = false;
    showFlag: string;
    headerTitle: string;
    //分页
    total: number;
    first: number = 0;
    msgs: any;
    detailInValue:string;
    updateInValue:string;
    businessScenceMaintain:BusinessScenceMaintain = new BusinessScenceMaintain();
    constructor(
        public confirmationService:ConfirmationService,
        public router:Router,
        public interfaceHttpService:InterfaceHttpService
    ) {
    }

    ngOnInit() {
        this.businessScenceMaintain.pageNum = 1;
        this.businessScenceMaintain.pageSize = 10;
        this.queryClick()
    }
    //新增
    add() {
        this.showFlag = '1';
        this.headerTitle = '新增';
        this.updateDisplay = true;

    }
    //新增回调
    addCall (param) {
        this.updateDisplay = false;
        this.queryClick()
    }
    //查询
    queryClick() {
        this.interfaceHttpService.querBusinessSenceList(this.businessScenceMaintain).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.businessSenceList;
                this.total = data.totalNum
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }
    //重置
    reClean() {
        this.businessScenceMaintain.businessSenceName = '';
    }
    //页面集设置
    deMaintain(item) {
        this.router.navigate(['/pages/tzb/page-layout/business-scence/business-data-set'],{ queryParams: { businessSenceId: item.businessSenceId}})
    }
    //删除
    detele(item) {
        this.confirmationService.confirm({
            message: '确定要删除吗?',
            accept: () => {
               
            }
        });
    }
    //修改
    deupdate (param) {
        this.showFlag = '2';
        this.headerTitle = '修改';
        this.updateDisplay = true;
        this.updateInValue = param;
    }
    //修改回调
    updateCall (event) {
        this.updateDisplay = false;
        this.queryClick()
    }
    //详情
    deDetail (param) {
        this.detailDisplay=true;
        this.detailInValue = param.businessSenceId
    }

    //详情回掉
    detailCall(event) {
        this.detailDisplay=false;
    }
    
    //分页
    paginate(event) {
        this.businessScenceMaintain.pageSize = event.rows;//每页显示的条数
        let currentPage = event.page + 1; //当前页
        this.businessScenceMaintain.pageNum = currentPage;
        this.first = event.page * event.rows;
        this.queryClick();//调用查询的方法
    }
}