import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BusinessDataSetComponent } from './business-scence-maintain/business-data-set/business-data-set.component';
import { BusinessScenceMaintainComponent } from './business-scence-maintain/business-scence-maintain.component';



export const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: 'business-scence-maintain',
                component: BusinessScenceMaintainComponent,
            },
            {
                path: 'business-data-set',
                component:BusinessDataSetComponent
                
            }
          
        ]
    }
]
export const BusinessScenceRouting: ModuleWithProviders = RouterModule.forChild(routes);